import React from "react";
import { Card, CardText, CardBody } from 'reactstrap';
import {Link} from 'react-router-dom'

   function RenderSalary ({staff, onClick}) { 
       return (
            <Card >
                <h2 > {staff.name} </h2>
                <CardBody >
                    <CardText> M nhn vin : {(staff.id) } </CardText>
                    <CardText> H s lng : {Math.trunc(staff.salaryScale)}</CardText>
                    <CardText> S ngy lm thm : {staff.overTime} </CardText>
                    <CardText className="khung"> Lng : {Math.trunc(staff.salaryScale) * 3000000 + staff.overTime *200000} </CardText>
                </CardBody>
            </Card>
       )
   }
  
        
    ///////////////hin th //////////
    const Salary = ({staffs}) =>  {
        const salary = staffs.map ((staff) => {
            return (    
                <div key={staff.id} className = 'col-sm-6 col-xs-12 col-md-4 mt-4 '>
                    <RenderSalary staff={staff}  />
                </div>
            )
        })
        
        
        return (
            <div className="container">
                 <div className="row">
                    <p className="pay"><Link to='/stafflist'>Nhn vin </Link>/ Bng lng</p>
                </div>
                <div className="row">
                    {salary}
                </div>
            </div>
        )
    }


export default Salary;