import React from 'react';
import { Modal, ModalHeader, ModalBody, ModalFooter, FormFeedback, Form } from 'reactstrap';
import { Input, Col, FormGroup, Button, Label } from 'reactstrap';

class ModalAddStaff extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            name: '',
            doB: '',
            startDate: '',
            department: '',
            salaryScale: 1,
            leaveTime: 0,
            overTime: 0,
            modal: false,
            search: '',
            touched: {
                name: false,
                doB: false,
                startDate: false,
                department: false,
            }
        };
        console.log(this.state.doB)
        this.toggleModal = this.toggleModal.bind(this)
        this.handleInputChange = this.handleInputChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this)
        this.handleBlur = this.handleBlur.bind(this)
    }

    toggleModal() {
        this.setState({
            modal: !this.state.modal,
        });
    }

    handleInputChange(e) {
        const target = e.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;

        console.log(value)
        this.setState({
            [name]: value,
        })

        console.log(this.state.date)
        console.log(this.state.start)
        console.log(target.name)
    }

    handleSubmit(event) {
        let item = {
            name: this.state.name,
            doB: this.state.doB,
            startDate: this.state.startDate,
            department: this.state.department,
        }
        // if(!this.validate()){
        this.props.onClickSubmit(item) 
    // }
        event.preventDefault()

    }

    handleBlur = (field) => (event) => {
        this.setState({
            touched: { ...this.state.touched, [field]: true }
        });
    }

    validate(name, doB, startDate, department) {
        const errors = {
            name: '',
            doB: '',
            startDate: '',
            salaryScale: '',
            department: '',
        }

        if (this.state.touched.name && name.length < 3)
            errors.name = 'Name is should be more 3 character'
        else if (this.state.touched.name && name.length > 15)
            errors.name = 'Name is shouble less or equal 15 character'

        return errors;
    }

    ///////////////hin th //////////
    render() {
        const errors = this.validate(this.state.name, this.state.doB,
            this.state.startDate, this.state.department)
        return (
            <FormGroup row>
                <Col md={5}>
                    <Button color='danger' onClick={this.toggleModal}>
                        +
                    </Button>
                    <Modal
                        isOpen={this.state.modal}
                        toggle={this.toggleModal}
                        className={this.props.className}>
                        <ModalHeader toggle={this.toggleModal}>
                            Thnh vin
                        </ModalHeader>
                        <Form onSubmit={this.handleSubmit}>
                            <ModalBody>
                                <FormGroup row >
                                    <Label htmlFor='name' md={3}> Tn</Label>
                                    <Col md={9}>
                                        <Input type='text' name='name' id='name' value={this.state.name}
                                            valid={errors.name === ''}
                                            invalid={errors.name !== ''}
                                            onChange={this.handleInputChange}
                                            onBlur={this.handleBlur('name')}    />
                                        <FormFeedback>{errors.name}</FormFeedback>
                                    </Col>
                                </FormGroup>
                                <FormGroup row>
                                    <Label htmlFor='dob' md={3}>Ngy sinh</Label>
                                    <Col md={9}>
                                        <Input type='date' name='doB' id='date' value={this.state.doB}
                                            // valid={errors.doB === ''}
                                            // invalid={errors.doB !== ''}
                                            onChange={this.handleInputChange}
                                            onBlur={this.handleBlur('doB')} />
                                        <FormFeedback>{errors.doB}</FormFeedback>
                                    </Col>
                                </FormGroup>
                                <FormGroup row>
                                    <Label htmlFor='start' md={3}>Ngy vo cng ty </Label>
                                    <Col md={9}>
                                        <Input type='date' name='startDate' id='start'
                                            value={this.state.startDate}
                                            // valid={errors.startDate === ''}
                                            // invalid={errors.startDate !== ''}
                                            onChange={this.handleInputChange}
                                            onBlur={this.handleBlur('startDate')} />
                                        <FormFeedback>{errors.startDate}</FormFeedback>
                                    </Col>  
                                </FormGroup>
                                <FormGroup row>
                                    <Label md={3} htmlFor='department'>Phng ban </Label>
                                    <Col md={9}>
                                        <Input type='select' name='department' id='departmnet'
                                            onChange={this.handleInputChange}
                                            value={this.state.department} >
                                            <option>Sale</option>
                                            <option>HR</option>
                                            <option>Marketing</option>
                                            <option>IT </option>
                                            <option>Finance</option>
                                        </Input>
                                    </Col>
                                </FormGroup>
                                <FormGroup row>
                                    <Label htmlFor='Leave' md={3}>H s lng </Label>
                                    <Col md={9}>
                                        <Input type='scale' value={this.state.salaryScale}
                                            // valid={errors.salaryScale === ''}
                                            //  invalid={errors.salaryScale !== ''}
                                            onChange={this.handleInputChange}
                                            onBlur={this.handleBlur} />
                                        <FormFeedback>{errors.salaryScale}</FormFeedback>
                                    </Col>
                                </FormGroup>
                                <FormGroup row>
                                    <Label htmlFor='Leave' md={3}>S ngy ngh cn li </Label>
                                    <Col md={9}>
                                        <Input md={1} type='leave' value={this.state.leaveTime}
                                            // valid={errors.name === ''}
                                            // invalid={errors.name !== ''}
                                            onChange={this.handleInputChange}
                                            onBlur={this.handleBlur} />
                                        <FormFeedback>{errors.leaveTime}</FormFeedback>
                                    </Col>
                                </FormGroup>
                                <FormGroup row>
                                    <Label htmlFor='Over' md={3}>S ngy  lm thm  </Label>
                                    <Col md={9}>
                                        <Input type='over' value={this.state.overTime}
                                            // valid={errors.name === ''}
                                            // invalid={errors.name !== ''}
                                            onChange={this.handleInputChange}
                                            onBlur={this.handleBlur} />
                                        <FormFeedback>{errors.overTime}</FormFeedback>
                                    </Col>
                                </FormGroup>
                            </ModalBody>
                            <ModalFooter >
                                <Button color='primary' type='submit'
                                >
                                    Thm nhn vin
                                </Button>{' '}
                                <Button color='danger' onClick={this.toggleModal}>
                                    Cancel
                                </Button>
                            </ModalFooter>
                        </Form>

                    </Modal>
                </Col>
                <Col md={{ size: 5, offset: 4 }}>
                    <Input type='search' onChange={(e) => this.setState({ search: e.target.value })}
                    />
                </Col>
                <Button color='primary' md={{ size: 1 }} forHtml='Tm'
                    onClick={() => this.props.handleSearch(this.state.search)}>Tm {' '}</Button>
            </FormGroup>
        );
    }
}

export default ModalAddStaff;